import React, { useState, useEffect } from 'react';
import axios from "axios";
import CssBaseline from '@mui/material/CssBaseline';
import { useSelector } from 'react-redux';

import Login from './pages/Login';
import Homepage from './pages/Homepage';
import Drives from './pages/Drives';
import Hitchhiker from './pages/Hitchhiker';
import Driver from './pages/Driver';
import Register from './pages/Register';
import Home from './pages/Home';
import Chat from './pages/Chatt';
import Topmenu from './components/Topmenu';
import FlashMessage from './components/Flashmessage';
import { createTheme, ThemeProvider } from '@mui/material/styles';

import {
  BrowserRouter as Router,
  Switch,
  Route,
  Redirect,
  RouteProps
} from "react-router-dom";

const theme = createTheme({palette: {
  mode: 'dark',
},});

const App = (props:any) => {
  const [loggedInStatus, setLoggedInStatus] = useState(false);
  const [flashMessage, setFlashMessage] = useState("");

  //const user = useSelector(selectUser);

  useEffect(() => {
    checkLoginStatus();
    console.log("constructor");
    console.log(loggedInStatus);
  });

  const checkLoginStatus = async () => {
    const requestOptions = {
      method: 'POST',
      headers: { 'Content-Type': 'application/json' },
      body: { 'token' : localStorage.getItem('token') }
    };

    axios
      .post("http://127.0.0.1:8000/loggedin/", requestOptions)
      .then(response => {
        if (
          response.data.loggedIn === "true"
        ) {
          setLoggedInStatus(true);
        } else {
          setLoggedInStatus(false);
        }
      })
      .catch(error => {
        console.log("check login error", error);
      });
  }

  const handleSuccessfulAuth = (data:any) => {
    localStorage.setItem("token", data.user.token);
    setLoggedInStatus(true);
  }

  const handleFleshmessage = (message:string) => {
    setFlashMessage(message);
    sleep(function(){
      setFlashMessage("");
    });
  }

  function timeout(ms:any) {
      return new Promise(resolve => setTimeout(resolve, ms));
  }

  async function sleep(fn:any, ...args:any[]) {
      await timeout(3000);
      return fn(...args);
  }

  const handleLogout = () => {
    localStorage.setItem('token', '');
    localStorage.setItem('userEmail', '');

    setLoggedInStatus(false);
    window.location.href = '/';
  }

  interface PrivateRouteProps extends RouteProps {
    // tslint:disable-next-line:no-any

}

const PrivateRoute = (props: PrivateRouteProps) => {
    const { children, ...rest } = props;
    console.log(loggedInStatus);
    console.log("route");
      return (
        <Route
            {...rest}
            render={(routeProps) =>
              loggedInStatus === true ? (
                  children
                ) : (
                  <Redirect to="/login" />
                )
            }
        />
    
    );
    
};

  return (
    <Router>
      <ThemeProvider theme={theme}>  
      <CssBaseline />
      <div className="App">
      <Topmenu loggedInStatus={loggedInStatus} handleLogout={handleLogout} />
      <FlashMessage flashMessage={flashMessage} />
      <main className="px-3">
        <Switch>
        <PrivateRoute path="/chat">
          <Chat />
        </PrivateRoute>
        <Route exact path="/drives">
          <Drives />
        </Route>
          <Route path="/register">
            {loggedInStatus === false 
            ? <Register /> 
            : <Redirect to="/home" />}
          </Route>
          <Route path="/login">
            {loggedInStatus === false 
            ? <Login handleFleshmessage={handleFleshmessage} handleSuccessfulAuth={handleSuccessfulAuth} /> 
            : <Redirect to="/home" />}
          </Route>
          <Route path="/driver">
            <Driver />
          </Route>
          <Route path="/home">
            {loggedInStatus === true 
            ? <Home /> 
            : <Redirect to="/" />}
          </Route> 
          <Route path="/hitchhiker">
            {loggedInStatus === true 
            ? <Hitchhiker />
            : <Redirect to="/" />} 
          </Route>
          
          <Route path="/">
            {loggedInStatus === false 
            ? <Homepage />
            : <Redirect to="/home" />}   
          </Route>
        </Switch>
      </main> 
      
      <br /><br /><br /><br /><br />
    </div>
    </ThemeProvider>
    </Router>
  );
}

export default App;
